@import "_flex";
@import "spectrum-origins/spectrum-light.scss";

// Create or remove breakpoints for your project
// Syntax:
// name SIZErem,
$flexboxgrid-breakpoints:
  xs $spectrum-global-layout-breakpoint-xsmall,
  sm $spectrum-global-layout-breakpoint-small,
  md $spectrum-global-layout-breakpoint-medium,
  lg $spectrum-global-layout-breakpoint-large,
  xl $spectrum-global-layout-breakpoint-large;

.spectrum-grid {
  box-sizing: border-box;
  padding: 0 $spectrum-alias-grid-gutter-xsmall;

  @media screen and (min-width: $spectrum-global-layout-breakpoint-xsmall) {
    padding: 0 $spectrum-alias-grid-gutter-xsmall;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-small) {
    padding: 0 $spectrum-alias-grid-gutter-small;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-medium) {
    padding: 0 $spectrum-alias-grid-gutter-medium;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-large) {
    padding: 0 $spectrum-alias-grid-gutter-large;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-xlarge) {
    padding: 0 $spectrum-alias-grid-gutter-xlarge;
  }
}

.spectrum-grid--fixed {
  @extend .spectrum-grid;
  max-width: $spectrum-alias-grid-gutter-xsmall * 2 + $spectrum-global-layout-grid-fixed-max-width;
  margin: 0 auto;

  @media screen and (min-width: $spectrum-global-layout-breakpoint-xsmall) {
    max-width: $spectrum-alias-grid-gutter-xsmall * 2 + $spectrum-global-layout-grid-fixed-max-width;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-small) {
    max-width: $spectrum-alias-grid-gutter-small * 2 + $spectrum-global-layout-grid-fixed-max-width;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-medium) {
    max-width: $spectrum-alias-grid-gutter-medium * 2 + $spectrum-global-layout-grid-fixed-max-width;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-large) {
    max-width: $spectrum-alias-grid-gutter-large * 2 + $spectrum-global-layout-grid-fixed-max-width;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-xlarge) {
    max-width: $spectrum-alias-grid-gutter-xlarge * 2 + $spectrum-global-layout-grid-fixed-max-width;
  }
}

.spectrum-grid--fluid {
  @extend .spectrum-grid;
  width: $spectrum-global-layout-grid-fluid-width;
  margin: 0 auto;
}

.spectrum-grid-row {
  box-sizing: border-box;
  @include flexbox();
  @include flex(0, 1, auto);
  @include flex-direction(row);
  @include flex-wrap(wrap);
  margin-right: $spectrum-alias-grid-gutter-xsmall * 0.5 * -1;
  margin-left: $spectrum-alias-grid-gutter-xsmall * 0.5 * -1;

  @media screen and (min-width: $spectrum-global-layout-breakpoint-xsmall) {
    margin-right: $spectrum-alias-grid-gutter-xsmall * 0.5 * -1;
    margin-left: $spectrum-alias-grid-gutter-xsmall * 0.5 * -1;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-small) {
    margin-right: $spectrum-alias-grid-gutter-small * 0.5 * -1;
    margin-left: $spectrum-alias-grid-gutter-small * 0.5 * -1;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-medium) {
    margin-right: $spectrum-alias-grid-gutter-medium * 0.5 * -1;
    margin-left: $spectrum-alias-grid-gutter-medium * 0.5 * -1;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-large) {
    margin-right: $spectrum-alias-grid-gutter-large * 0.5 * -1;
    margin-left: $spectrum-alias-grid-gutter-large * 0.5 * -1;
 }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-xlarge) {
    margin-right: $spectrum-alias-grid-gutter-xlarge * 0.5 * -1;
    margin-left: $spectrum-alias-grid-gutter-xlarge * 0.5 * -1;
  }
}

.spectrum-grid-row.spectrum-grid-reverse {
  @include flex-direction(row-reverse);
}

.spectrum-grid-col.spectrum-grid-reverse {
  @include flex-direction(column-reverse);
}

@mixin flexboxgrid-sass-col-common {
  box-sizing: border-box;

  // split @include flex(0, 0, auto) into individual props
  @include flex-grow(0);
  @include flex-shrink(0);

  // we leave @include flex-basis(auto) out of common because
  // in some spots we need it and some we dont
  // more why here: https://github.com/kristoferjoseph/flexboxgrid/issues/126

  padding-right: $spectrum-alias-grid-gutter-xsmall* .5;
  padding-left: $spectrum-alias-grid-gutter-xsmall* .5;

  @media screen and (min-width: $spectrum-global-layout-breakpoint-xsmall) {
    padding-right: $spectrum-alias-grid-gutter-xsmall* .5;
    padding-left: $spectrum-alias-grid-gutter-xsmall* .5;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-small) {
    padding-right: $spectrum-alias-grid-gutter-small* .5;
    padding-left: $spectrum-alias-grid-gutter-small* .5;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-medium) {
    padding-right: $spectrum-alias-grid-gutter-medium* .5;
    padding-left: $spectrum-alias-grid-gutter-medium* .5;
  }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-large) {
    padding-right: $spectrum-alias-grid-gutter-large* .5;
    padding-left: $spectrum-alias-grid-gutter-large* .5;
 }

  @media screen and (min-width: $spectrum-global-layout-breakpoint-xlarge) {
    padding-right: $spectrum-alias-grid-gutter-xlarge* .5;
    padding-left: $spectrum-alias-grid-gutter-xlarge* .5;
  }
}

$name: xs;
.spectrum-grid-col-#{$name} {
  @include flexboxgrid-sass-col-common;
  @include flex-basis(auto);
}
@for $i from 1 through $spectrum-global-layout-grid-columns {
  .spectrum-grid-col-#{$name}-#{$i} {
    @include flexboxgrid-sass-col-common;
    @include flex-basis(100% / $spectrum-global-layout-grid-columns * $i);
    max-width: 100% / $spectrum-global-layout-grid-columns * $i;
  }
}
@for $i from 0 through $spectrum-global-layout-grid-columns {
  .spectrum-grid-col-#{$name}-offset-#{$i} {
    @include flexboxgrid-sass-col-common;
    @if $i == 0 {
      margin-left: 0;
    } @else {
      margin-left: 100% / $spectrum-global-layout-grid-columns * $i;
    }
  }
}
.spectrum-grid-col-#{$name} {
  @include flex-grow(1);
  @include flex-basis(0);
  max-width: 100%;
}
.spectrum-grid-start-#{$name} {
  @include justify-content(flex-start);
  text-align: left;
}

.spectrum-grid-center-#{$name} {
  @include justify-content(center);
  text-align: center;
}

.spectrum-grid-end-#{$name} {
  @include justify-content(flex-end);
  text-align: right;
}

.spectrum-grid-top-#{$name} {
  @include align-items(flex-start);
}

.spectrum-grid-middle-#{$name} {
  @include align-items(center);
}

.spectrum-grid-bottom-#{$name} {
  @include align-items(flex-end);
}

.spectrum-grid-around-#{$name} {
  @include justify-content(space-around);
}

.spectrum-grid-between-#{$name} {
  @include justify-content(space-between);
}

.spectrum-grid-first-#{$name} {
  order: -1;
}

.spectrum-grid-last-#{$name} {
  order: 1;
}


@each $breakpoint in $flexboxgrid-breakpoints {
  $name: nth($breakpoint, 1);
  $size: nth($breakpoint, 2);
  $container: nth($breakpoint, 2);
  @media only screen and (min-width: $size) {
    .spectrum-grid-container {
      width: $container;
    }

    .spectrum-grid-col-#{$name} {
      @include flexboxgrid-sass-col-common;
      @include flex-basis(auto);
    }
    @for $i from 1 through $spectrum-global-layout-grid-columns {
      .spectrum-grid-col-#{$name}-#{$i} {
        @include flexboxgrid-sass-col-common;
        @include flex-basis(100% / $spectrum-global-layout-grid-columns * $i);
        max-width: 100% / $spectrum-global-layout-grid-columns * $i;
      }
    }
    @for $i from 0 through $spectrum-global-layout-grid-columns {
      .spectrum-grid-col-#{$name}-offset-#{$i} {
        @include flexboxgrid-sass-col-common;
        @if $i == 0 {
          margin-left: 0;
        } @else {
          margin-left: 100% / $spectrum-global-layout-grid-columns * $i;
        }
      }
    }
    .spectrum-grid-col-#{$name} {
      @include flex-grow(1);
      @include flex-basis(0);
      max-width: 100%;
    }
    .spectrum-grid-start-#{$name} {
      @include justify-content(flex-start);
      text-align: left;
    }

    .spectrum-grid-center-#{$name} {
      @include justify-content(center);
      text-align: center;
    }

    .spectrum-grid-end-#{$name} {
      @include justify-content(flex-end);
      text-align: right;
    }

    .spectrum-grid-top-#{$name} {
      @include align-items(flex-start);
    }

    .spectrum-grid-middle-#{$name} {
      @include align-items(center);
    }

    .spectrum-grid-bottom-#{$name} {
      @include align-items(flex-end);
    }

    .spectrum-grid-around-#{$name} {
      @include justify-content(space-around);
    }

    .spectrum-grid-between-#{$name} {
      @include justify-content(space-between);
    }

    .spectrum-grid-first-#{$name} {
      order: -1;
    }

    .spectrum-grid-last-#{$name} {
      order: 1;
    }
  }
}
